import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import config from '../config';

type SearchQuery = {
	search: string;
	selectedStatuses: Array<Object>;
};

export const getValidSearchQuery = ({
	search,
	selectedStatuses,
}: SearchQuery) => {
	const validStatues = selectedStatuses.map(status => _get(status, 'Id'));
	const searchText =
		!_isEmpty(validStatues) && !search ? config.defaultText.query : search;
	return {
		query: searchText.trim(),
		statusIds: _isEmpty(validStatues) ? undefined : validStatues,
	};
};
